"use client";

import { useState } from "react";
import Sidebar from "@/components/_sidebar/sidebar";
import MainContent from "@/components/_main/main-content";

export default function Page() {
  // Estado controlado na página pai
  const [activeItem, setActiveItem] = useState("home");
  const [isExpanded, setIsExpanded] = useState(false);

  const handleItemClick = (itemId: string) => {
    setActiveItem(itemId);
    // Fecha o menu no mobile após seleção
    if (window.innerWidth < 768) {
      setIsExpanded(false);
    }
  };

  return (
    <div className="min-h-screen bg-zinc relative overflow-hidden">

      <div className="absolute inset-0 pointer-events-none">

      </div>

      {/* Sidebar - componente visual sem lógica */}
      <Sidebar
        isExpanded={isExpanded}
        setIsExpanded={setIsExpanded}
        activeItem={activeItem}
        onItemClick={handleItemClick}
      />

      {/* Main Content */}
      <MainContent activeItem={activeItem} isExpanded={isExpanded} />
    </div>
  );
}
